//route.js
let directionsService; // –¥–ª—è –ø–æ–±—É–¥–æ–≤–∏ –º–∞—Ä—à—Ä—É—Ç—É
let directionsRenderer; // –¥–ª—è –≤—ñ–∑—É–∞–ª—ñ–∑–∞—Ü—ñ—ó –º–∞—Ä—à—Ä—É—Ç—É –Ω–∞ –º–∞–ø—ñ
let distanceService; // –¥–ª—è –ø—ñ–¥—Ä–∞—Ö—É–Ω–∫—É –≤—ñ–¥—Å—Ç–∞–Ω—ñ —Ç–∞ —á–∞—Å—É
let fromPlace = null; // –º—ñ—Å—Ü–µ –ø–æ—á–∞—Ç–∫—É (A)
let toPlace = null; // –º—ñ—Å—Ü–µ –ø—Ä–∏–∑–Ω–∞—á–µ–Ω–Ω—è (B)
let mapRef = null; // —Å–∞–º–∞ –∫–∞—Ä—Ç–∞
let mapKmEl = null; // –µ–ª–µ–º–µ–Ω—Ç, –∫—É–¥–∏ –≤—Å—Ç–∞–≤–ª—è—î—Ç—å—Å—è –∫–º
let mapTimeEl = null; // –µ–ª–µ–º–µ–Ω—Ç, –∫—É–¥–∏ –≤—Å—Ç–∞–≤–ª—è—î—Ç—å—Å—è —á–∞—Å

// –Ü–Ω—ñ—Ü—ñ–∞–ª—ñ–∑–∞—Ü—ñ—è –º–∞—Ä—à—Ä—É—Ç—ñ–≤ —ñ –≤—ñ–¥–æ–±—Ä–∞–∂–µ–Ω–Ω—è –Ω–∞ –∫–∞—Ä—Ç—ñ
export function initRouting({
  map,
  DirectionsService,
  DirectionsRenderer,
  DistanceMatrixService,
}) {
  directionsService = new DirectionsService(); // –¥–ª—è —Å—Ç–≤–æ—Ä–µ–Ω–Ω—è –º–∞—Ä—à—Ä—É—Ç—É
  directionsRenderer = new DirectionsRenderer({ suppressMarkers: true }); // –Ω–µ –º–∞–ª—é—î –∞–≤—Ç–æ–º–∞—Ç–∏—á–Ω–æ –º–∞—Ä–∫–µ—Ä–∏
  directionsRenderer.setMap(map); // –ø–æ–∫–∞–∑—É—î –º–∞—Ä—à—Ä—É—Ç –Ω–∞ –∫–∞—Ä—Ç—ñ

  distanceService = new DistanceMatrixService(); // –¥–ª—è –ø—ñ–¥—Ä–∞—Ö—É–Ω–∫—É –∫–º/—á–∞—Å—É
  mapRef = map;

  mapKmEl = document.querySelector('.map-km'); // HTML-–µ–ª–µ–º–µ–Ω—Ç–∏
  mapTimeEl = document.querySelector('.map-time');
}

// –í—Å—Ç–∞–Ω–æ–≤–ª—é—î —Ç–æ—á–∫—É –ø–æ—á–∞—Ç–∫—É –º–∞—Ä—à—Ä—É—Ç—É (A) —ñ –æ–Ω–æ–≤–ª—é—î –º–∞—Ä—à—Ä—É—Ç
export function setFromPlace(place) {
  fromPlace = place;
  calculateAndDisplayRoute();
}

// –í—Å—Ç–∞–Ω–æ–≤–ª—é—î —Ç–æ—á–∫—É –ø—Ä–∏–∑–Ω–∞—á–µ–Ω–Ω—è –º–∞—Ä—à—Ä—É—Ç—É (B) —ñ –æ–Ω–æ–≤–ª—é—î –º–∞—Ä—à—Ä—É—Ç
export function setToPlace(place) {
  toPlace = place;
  calculateAndDisplayRoute();
}

// –ë—É–¥—É—î –º–∞—Ä—à—Ä—É—Ç —ñ —Ä–æ–∑—Ä–∞—Ö–æ–≤—É—î –≤—ñ–¥—Å—Ç–∞–Ω—å —ñ —Ç—Ä–∏–≤–∞–ª—ñ—Å—Ç—å –ø–æ—ó–∑–¥–∫–∏
export function calculateAndDisplayRoute() {
  if (!fromPlace || !toPlace) return; // —è–∫—â–æ –Ω–µ –≤–∏–±—Ä–∞–Ω—ñ –æ–±–∏–¥–≤—ñ —Ç–æ—á–∫–∏ ‚Äî –Ω—ñ—á–æ–≥–æ –Ω–µ —Ä–æ–±–∏–º–æ

  // –ü–æ–±—É–¥–æ–≤–∞ –º–∞—Ä—à—Ä—É—Ç—É –Ω–∞ –º–∞–ø—ñ
  directionsService.route(
    {
      origin: fromPlace.geometry.location, // –∫–æ–æ—Ä–¥–∏–Ω–∞—Ç–∏ —Ç–æ—á–∫–∏ A
      destination: toPlace.geometry.location, // –∫–æ–æ—Ä–¥–∏–Ω–∞—Ç–∏ —Ç–æ—á–∫–∏ B
      travelMode: google.maps.TravelMode.DRIVING, // —Ç–∏–ø —Ç—Ä–∞–Ω—Å–ø–æ—Ä—Ç—É
    },
    (result, status) => {
      if (status === google.maps.DirectionsStatus.OK) {
        directionsRenderer.setDirections(result); // –º–∞–ª—é—î–º–æ –º–∞—Ä—à—Ä—É—Ç
      } else {
        console.error('Directions error:', status);
      }
    }
  );

  // –û–±—Ä–∞—Ö—É–Ω–æ–∫ –≤—ñ–¥—Å—Ç–∞–Ω—ñ —ñ —á–∞—Å—É
  distanceService.getDistanceMatrix(
    {
      origins: [fromPlace.geometry.location],
      destinations: [toPlace.geometry.location],
      travelMode: google.maps.TravelMode.DRIVING,
    },
    (response, status) => {
      if (status === google.maps.DistanceMatrixStatus.OK) {
        const element = response.rows[0].elements[0];
        mapKmEl.textContent = element.distance.text; // –≤—Å—Ç–∞–≤–ª—è—î–º–æ –∫–º —É HTML
        mapTimeEl.textContent = element.duration.text; // –≤—Å—Ç–∞–≤–ª—è—î–º–æ —á–∞—Å —É HTML
      } else {
        console.error('DistanceMatrix error:', status);
      }
    }
  );
}

// —Ñ—É–Ω–∫—Ü—ñ—è –æ—á–∏—â–µ–Ω–Ω—è –º–∞—Ä—à—Ä—É—Ç—É
export function clearRoute() {
  if (directionsRenderer) {
    directionsRenderer.set('directions', null); // –æ—á–∏—â–∞—î –º–∞—Ä—à—Ä—É—Ç
  }

  if (mapKmEl) mapKmEl.textContent = '';
  if (mapTimeEl) mapTimeEl.textContent = '';

  // –æ—á–∏—â–∞—î –≤–Ω—É—Ç—Ä—ñ—à–Ω—ñ –∑–º—ñ–Ω–Ω—ñ
  fromPlace = null;
  toPlace = null;
}
