//map.js
// –ó–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è Google Maps API –¥–∏–Ω–∞–º—ñ—á–Ω–æ
import { loadGoogleMaps } from '../api/loader.js';
// –§—É–Ω–∫—Ü—ñ—è, —è–∫–∞ –Ω–∞–ª–∞—à—Ç–æ–≤—É—î —ñ–Ω–ø—É—Ç-–ø–æ–ª—è –∞–¥—Ä–µ—Å (Autocomplete, –ø–æ–¥—ñ—ó)
import { setupInputs } from './inputs.js';
// –§—É–Ω–∫—Ü—ñ—è, —è–∫–∞ –Ω–∞–ª–∞—à—Ç–æ–≤—É—î –ª–æ–≥—ñ–∫—É –º–∞—Ä—à—Ä—É—Ç—ñ–≤ (–ø–æ–±—É–¥–æ–≤–∞, –≤—ñ–¥—Å—Ç–∞–Ω—å, —á–∞—Å)
import { initRouting } from './route.js';
// –í—ñ–¥–Ω–æ–≤–ª—é—î –º–∞—Ä–∫–µ—Ä–∏, —è–∫—ñ –±—É–ª–∏ –∑–±–µ—Ä–µ–∂–µ–Ω—ñ –≤ localStorage
import { restoreMarkers } from './markers.js';

// –ì–æ–ª–æ–≤–Ω–∞ —Ñ—É–Ω–∫—Ü—ñ—è ‚Äî –≤–∏–∫–ª–∏–∫–∞—î—Ç—å—Å—è –∑ main.js
export async function initMap() {
  // 1. –ó–∞–≤–∞–Ω—Ç–∞–∂—É—î–º–æ —Å–∫—Ä–∏–ø—Ç Google Maps API
  await loadGoogleMaps();

  // 2. –Ü–º–ø–æ—Ä—Ç—É—î–º–æ –ø–æ—Ç—Ä—ñ–±–Ω—ñ —á–∞—Å—Ç–∏–Ω–∏ Google Maps API (—Ü–µ –∞—Å–∏–Ω—Ö—Ä–æ–Ω–Ω–æ)
  const [
    { Map }, // –î–ª—è —Å—Ç–≤–æ—Ä–µ–Ω–Ω—è —Å–∞–º–æ—ó –∫–∞—Ä—Ç–∏
    { Marker }, // –î–ª—è –º–∞—Ä–∫–µ—Ä—ñ–≤ A —ñ B
    { Autocomplete }, // –î–ª—è –ø—ñ–¥–∫–∞–∑–æ–∫ —É –ø–æ–ª—è—Ö –≤–≤–æ–¥—É
    { DirectionsService, DirectionsRenderer, DistanceMatrixService }, // –î–ª—è –º–∞—Ä—à—Ä—É—Ç—ñ–≤
  ] = await Promise.all([
    google.maps.importLibrary('maps'),
    google.maps.importLibrary('marker'),
    google.maps.importLibrary('places'),
    google.maps.importLibrary('routes'),
  ]);

  // 3. –¶–µ–Ω—Ç—Ä –∫–∞—Ä—Ç–∏ ‚Äî –ü—Ä–∞–≥–∞
  const defaultLocation = { lat: 50.0755, lng: 14.4378 };

  // 4. –°—Ç–≤–æ—Ä—é—î–º–æ —Å–∞–º—É –∫–∞—Ä—Ç—É —Ç–∞ –¥–æ–¥–∞—î–º–æ —ó—ó –≤ –µ–ª–µ–º–µ–Ω—Ç –∑ id="map"
  const map = new Map(document.getElementById('map'), {
    center: defaultLocation,
    zoom: 13,
  });

  // 5. –§–æ—Ä–º—É—î–º–æ –æ–±'—î–∫—Ç —ñ–∑ —É—Å—ñ–º–∞ –ø–æ—Ç—Ä—ñ–±–Ω–∏–º–∏ –∫–ª–∞—Å–∞–º–∏ —Ç–∞ –∫–∞—Ä—Ç–æ—é
  const context = {
    map,
    Marker,
    Autocomplete,
    DirectionsService,
    DirectionsRenderer,
    DistanceMatrixService,
  };

  // 6. –Ü–Ω—ñ—Ü—ñ–∞–ª—ñ–∑—É—î–º–æ —ñ–Ω–ø—É—Ç–∏ –∞–¥—Ä–µ—Å (–≤–≤—ñ–¥, –∞–≤—Ç–æ–∑–∞–ø–æ–≤–Ω–µ–Ω–Ω—è, –∫–ª–∞—Ü–∞–Ω–Ω—è –ø–æ –∫–∞—Ä—Ç—ñ)
  setupInputs(context);
  // 7. –Ü–Ω—ñ—Ü—ñ–∞–ª—ñ–∑—É—î–º–æ —Å–µ—Ä–≤—ñ—Å–∏ –º–∞—Ä—à—Ä—É—Ç—ñ–≤ —Ç–∞ –æ–±—á–∏—Å–ª–µ–Ω–Ω—è –≤—ñ–¥—Å—Ç–∞–Ω—ñ
  initRouting(context);
  // 8. –í—ñ–¥–Ω–æ–≤–ª—é—î–º–æ –º–∞—Ä–∫–µ—Ä–∏ —Ç–∞ –¥–∞–Ω—ñ –∑ LocalStorage, —è–∫—â–æ –∫–æ—Ä–∏—Å—Ç—É–≤–∞—á —â–æ—Å—å –≤–≤–æ–¥–∏–≤ —Ä–∞–Ω—ñ—à–µ
  restoreMarkers(context);
}
