// loader.js
const API_key = 'AIzaSyDenJrU1OnfLreiz4i6a7dyoBeSLK02F7Y';

export const loadGoogleMaps = (g = {}) => {
  return new Promise((resolve, reject) => {
    const p = 'The Google Maps JavaScript API';
    const c = 'google';
    const q = '__ib__';
    const m = document;
    const b = window;
    b[c] = b[c] || {};
    const d = b[c].maps || (b[c].maps = {});
    const e = new URLSearchParams(); // –°—Ç–≤–æ—Ä—é—î –ø–∞—Ä–∞–º–µ—Ç—Ä–∏ –∑–∞–ø–∏—Ç—É –¥–ª—è URL
    e.set('key', API_key); // –î–æ–¥–∞—î API –∫–ª—é—á
    e.set('v', 'weekly'); // –í—Å—Ç–∞–Ω–æ–≤–ª—é—î –≤–µ—Ä—Å—ñ—é API
    e.set('callback', c + '.maps.' + q); // –î–æ–¥–∞—î —ñ–º‚Äô—è —Ñ—É–Ω–∫—Ü—ñ—ó, —è–∫—É –≤–∏–∫–ª–∏—á–µ Google, –∫–æ–ª–∏ —Å–∫—Ä–∏–ø—Ç –±—É–¥–µ –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–æ. –í –¥–∞–Ω–æ–º—É –≤–∏–ø–∞–¥–∫—É: google.maps.__ib__

    const script = m.createElement('script');
    script.src = `https://maps.${c}apis.com/maps/api/js?${e}`; // –§–æ—Ä–º—É—î URL –¥–ª—è —Å–∫—Ä–∏–ø—Ç–∞
    script.onerror = () => reject(Error(p + ' could not load.'));
    script.nonce = m.querySelector('script[nonce]')?.nonce || '';
    m.head.append(script); // –î–æ–¥–∞—î —Ç–µ–≥ <script> –¥–æ <head>, —â–æ–± –±—Ä–∞—É–∑–µ—Ä –∑–∞–≤–∞–Ω—Ç–∞–∂–∏–≤ Google Maps API

    d[q] = resolve;
  });
};
