const STORAGE_KEY = 'form-data';

// –ó–±–µ—Ä—ñ–≥–∞—î –∞–¥—Ä–µ—Å—É —Ç–∞ –∫–æ–æ—Ä–¥–∏–Ω–∞—Ç–∏. –ù–ï –ø–µ—Ä–µ–∑–∞–ø–∏—Å—É—î —Å—Ç–∞—Ä—ñ –∑–Ω–∞—á–µ–Ω–Ω—è, —è–∫—â–æ –Ω–æ–≤–∏—Ö –Ω–µ–º–∞—î
export function saveFormToLS({ fromAddress, toAddress, fromCoords, toCoords }) {
  const prev = loadFormFromLS(); // –ø–æ–ø–µ—Ä–µ–¥–Ω—ñ –¥–∞–Ω—ñ

  const data = {
    ...prev,
    ...(fromAddress && { 'get-in': fromAddress }),
    ...(toAddress && { 'get-out': toAddress }),
    ...(fromCoords && { 'get-in-coords': fromCoords }),
    ...(toCoords && { 'get-out-coords': toCoords }),
  };

  localStorage.setItem(STORAGE_KEY, JSON.stringify(data));
}

// –û—Ç—Ä–∏–º—É—î–º–æ –ø–æ–≤–Ω—ñ—Å—Ç—é –∑–±–µ—Ä–µ–∂–µ–Ω–∏–π –æ–±'—î–∫—Ç —Ñ–æ—Ä–º–∏
export function loadFormFromLS() {
  const raw = localStorage.getItem(STORAGE_KEY);
  return raw ? JSON.parse(raw) : {};
}

// –£–Ω—ñ–≤–µ—Ä—Å–∞–ª—å–Ω—ñ —Ñ—É–Ω–∫—Ü—ñ—ó (–º–æ–∂—É—Ç—å –≤–∏–∫–æ—Ä–∏—Å—Ç–æ–≤—É–≤–∞—Ç–∏—Å—å –¥–ª—è —ñ–Ω—à–∏—Ö —Ü—ñ–ª–µ–π)
export function loadLs(key) {
  return JSON.parse(localStorage.getItem(key));
}

export function saveLs(key, value) {
  localStorage.setItem(key, JSON.stringify(value));
}
